<script>
import GxIcon from '@components/_Core/Icons/GxIcon'
export default {
  name: 'GxWizard',
  data() {
    return {
      steps: null,
      stepActive: 0,
    }
  },
  props: {
    stepsProp: {
      type: Array,
      default: null,
    },
    stepActiveProp: {
      type: Number,
      default: null,
    },
  },
  components: {
    GxIcon,
  },
  computed: {
    getMaxSteps() {
      return this.steps.length
    },
  },
  created() {
    this.steps = this.stepsProp
    this.stepActive = this.stepActiveProp
  },
  methods: {
    nextStep() {
      this.stepActive++
    },
    backStep() {
      this.stepActive--
    },
  },
}
</script>
<template>
  <div class="flex">
    <div
      class="flex flex-col items-center w-41"
      v-for="(item, index) in this.steps"
      :key="index"
    >
      <div class="flex justify-between w-41">
        <div
          class="flex flex-col w-18 float-right "
          style="margin-top: 0.625rem; height: 1.5px"
          :class="{
            invisible: index === 0,
            'bg-primary': stepActive >= index + 1,
            'bg-gray-300': stepActive != index + 1 && !stepActive >= index + 1,
          }"
        >
        </div>
        <div
          class="flex flex-col w-6 h-6 pt-px border-solid border-2 border-primary rounded-full text-center"
          :class="{ 'bg-primary': stepActive - 1 >= index + 1 }"
        >
          <span
            class=" text-primary font-black"
            v-if="!(stepActive - 1 >= index + 1)"
          >
            {{ index + 1 }}</span
          >
          <GxIcon
            v-else-if="stepActive - 1 >= index + 1"
            class="rounded-full text-white pt-1 "
            :icon="'done'"
        /></div>
        <div
          class="flex w-18 float-right h-1"
          style="margin-top: 0.625rem; height: 1.5px"
          :class="{
            invisible: index + 1 === getMaxSteps,
            'bg-primary': stepActive >= index + 1,
            'bg-gray-300': stepActive != index + 1 && !stepActive >= index + 1,
          }"
        >
        </div>
      </div>
      <div
        class="flex flex-col mt-2 text-center"
        :class="{
          'text-primary': stepActive === index + 1 || stepActive >= index + 1,
          'font-black': stepActive === index + 1,
        }"
        >{{ item }}</div
      >
    </div>
  </div>
</template>
