<script>
import LayoutWebMobile from '@layouts/LayoutWebMobile'
import GxWizard from '@components/_CoreTaxes/Wizard/GxWizard'
import GxDropdown from '@components/_Core/Forms/GxDropdown'
import { GET_BEGINNING_PERIOD } from '@src/apollo/queries'
import { CONFIG_SET_BEGINNING_PERIOD } from '@src/apollo/mutations'
import GxCardModal from '@src/components/_Core/Cards/GxCardModal'

export default {
  name: 'InitialPeriod',
  components: {
    LayoutWebMobile,
    GxWizard,
    GxDropdown,
    GxCardModal,
  },
  data() {
    return {
      accountingFirmNumber: null,
      clientNumber: null,
      beginningPeriod: null,
      skipPeriods: false,
      loadingNext: false,
      loadingBack: false,
      errorDescription: '',
      error: null,
      steps: [
        'Elige tu catálogo',
        'Personaliza tu catálogo',
        'Periodo inicial',
        'Saldos iniciales',
        '¡Listo!',
      ],
      years: null,
      months: [
        'Enero',
        'Febrero',
        'Marzo',
        'Abril',
        'Mayo',
        'Junio',
        'Julio',
        'Agosto',
        'Septiembre',
        'Octubre',
        'Noviembre',
        'Diciembre',
      ],
      dateInitial: null,
      year: '',
      month: '',
    }
  },
  created() {
    this.clientNumber = localStorage.getItem('companyNumber')
    this.years = new Array()
    this.dateInitial = new Date()

    for (
      let yearIndex = this.dateInitial.getFullYear();
      yearIndex >= 1900;
      yearIndex--
    ) {
      this.years.push(String(yearIndex))
    }
    this.year = String(this.dateInitial.getFullYear())
    this.month = this.months[this.dateInitial.getMonth()]
  },
  computed: {
    getMonths() {
      let monthsCurrency = new Array()
      if (this.year === String(this.dateInitial.getFullYear())) {
        for (
          let monthIndex = this.dateInitial.getMonth();
          monthIndex >= 0;
          monthIndex--
        ) {
          monthsCurrency.push(this.months[monthIndex])
        }
      } else {
        return this.months
      }
      return monthsCurrency.reverse()
    },
  },
  apollo: {
    getPeriods: {
      query: GET_BEGINNING_PERIOD,
      variables() {
        return {
          clientNumber: [this.clientNumber],
        }
      },
      update(data) {
        this.accountingFirmNumber =
          data.myAccountingFirms[0].accountingFirmNumber
        this.beginningPeriod =
          data.myAccountingFirms[0].clients[0].accounting.beginningPeriod
        if (this.beginningPeriod !== null) this.setSelectsValues()

        // if (!localStorage.getItem('backBeginningBalance'))
        //   if (this.beginningPeriod !== null) this.goNext()
        //console.log(this.beginningPeriod)
      },
      skip() {
        return this.skipPeriods
      },
      fetchPolicy: 'no-cache',
    },
  },
  watch: {
    year(newYear) {
      //console.log('cambio')
      if (newYear === String(this.dateInitial.getFullYear()))
        this.month = this.months[this.dateInitial.getMonth()]
      else this.month = this.months[0]
    },
  },
  methods: {
    goBack() {
      this.$router.push({
        name: 'previewTemplate',
      })
    },
    setSelectsValues() {
      this.year = String(this.beginningPeriod).substring(0, 4)
      this.timeout = setTimeout(() => {
        this.month = this.months[
          Number(String(this.beginningPeriod).substring(6, 4) - 1)
        ]
      }, 5)
      this.$once('hook:destroyed', () => {
        clearTimeout(this.timeout)
      })
    },
    getMonthNumber() {
      let m = String(this.months.indexOf(this.month) + 1)
      return m.length === 1 ? '0' + m : m
    },
    async createBeginningPeriod() {
      this.loadingNext = true
      this.beginningPeriod = this.year + this.getMonthNumber()
      try {
        const response = await this.$apollo.mutate({
          mutation: CONFIG_SET_BEGINNING_PERIOD,
          variables: {
            accountingFirmNumber: this.accountingFirmNumber,
            clientNumber: this.clientNumber,
            beginningPeriod: this.beginningPeriod,
          },
        })
        this.loadingNext = false
        if (
          response.data.configSetBeginningPeriod.period !== null &&
          response.data.configSetBeginningPeriod.error === null
        ) {
          this.goNext()
        } else {
          this.errorDescription =
            response.data.configSetBeginningPeriod.error.message +
            ' - ' +
            response.data.configSetBeginningPeriod.error.details.join('')
        }
      } catch (error) {
        this.errorDescription = this.$utils.parseServerError(error)
        console.log(this.errorDescription)
        this.loadingNext = false

        return null
      }
    },
    async deleteBeginningPeriod() {},
    goNext() {
      this.$router.push({
        name: 'beginningBalance',
      })
    },
    goToPreviewTemplate(type) {
      this.$router.push({
        name: 'previewTemplate',
        params: { typeTemplateProps: type },
      })
    },
    closeModel() {
      this.errorDescription = ''
    },
  },
}
</script>
<template>
  <LayoutWebMobile icon="keyboard_backspace" :icon-click-handler="goBack">
    <div class="flex flex-col w-full items-center">
      <div class="flex flex-col text-xl mt-3"
        ><span class="font-black">Configuración Inicial</span></div
      >
      <div class="flex flex-col mt-10">
        <GxWizard :stepsProp="steps" :stepActiveProp="3" />
      </div>
      <div class="flex flex-col text-base mt-12"
        ><span class="font-black"
          >Selecciona el período inicial de esta contabilidad</span
        ></div
      >
      <!-- :iconLeft="'calendar_today'" -->
      <div class=" flex flex-col justify-between w-100">
        <div class="flex flex-col fixed w-48 ml-3">
          <GxDropdown
            watch-value
            v-model="year"
            class="mt-2"
            :options="this.years"
            placeholder="Año"
            label="Año *"
            :allow-empty="false"
          />
        </div>
        <div class="flex flex-col fixed ml-48 w-48 ">
          <GxDropdown
            watch-value
            v-model="month"
            class="mt-2"
            :options="this.getMonths"
            placeholder="Mes"
            label="Mes *"
            :allow-empty="false"
          />
        </div>
      </div>
    </div>
    <div
      class="flex fixed w-full bottom-0 border-t h-19 shadow-8 bg-white items-stretch justify-between"
    >
      <div class="flex flex-col w-full left-0">
        <div class="flex fixed w-50 inline">
          <GxButton
            :variant="!this.loadingBack"
            :ghost="!this.loadingBack"
            class="ml-10 mt-4 rounded-md gx-button"
            :loading="this.loadingBack"
            @click="goBack"
          >
            REGRESAR
          </GxButton>
        </div>
        <div class="flex fixed w-50 inline right-0">
          <GxButton
            :variant="!this.loadingNext"
            class="mr-10 mt-4 gx-button"
            @click="createBeginningPeriod"
            :loading="this.loadingNext"
          >
            SIGUIENTE
          </GxButton>
        </div>
      </div>
    </div>
    <GxCardModal
      v-if="errorDescription"
      :headline="'Se presentaron algunos errores al procesar la solicitud'"
      :caption="errorDescription"
      :card-img="$utils.alertImages.Oops"
      alt="Error"
      delayAnimation
    >
      <template v-slot:actions>
        <div class="flex justify-center mt-4">
          <GxButton @click="closeModel">ACEPTAR</GxButton>
        </div>
      </template>
    </GxCardModal>
  </LayoutWebMobile>
</template>
